import React, { useState, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { Form, Input, Spin, Button } from 'antd';
import { APP_PREFIX_PATH } from '../../configs/AppConfig';

const Profile = () => {
    const { username } = useParams();
    const history = useHistory();
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);

    useEffect(() => {
        let isMounted = true;
        const fetchUser = async () => {
            try {
                const response = await fetch(`https://jsonplaceholder.typicode.com/users`);
                const data = await response.json();
                const foundUser = data.find(user => user.username === username);
                if (isMounted) {
                    setUser(foundUser);
                    setLoading(false);
                }
            } catch (error) {
                console.error("Error fetching user:", error);
                if (isMounted) {
                    setLoading(false);
                }
            }
        };

        fetchUser();
        return () => { isMounted = false };
    }, [username]);

    const saveChanges = () => {
        setSaving(true);
        // Simulate a save to the server
        setTimeout(() => {
            setSaving(false);
            // Redirect to clients list page
            history.push(`${APP_PREFIX_PATH}/clients`);
        }, 1000);
    }

    if (loading || !user) {
        return <Spin size="large" />;
    }

    return (
        <Form>
            <Form.Item label="Name">
                <Input style={{ width: '33%' }} defaultValue={user.name} />
            </Form.Item>
            <Form.Item label="Email">
                <Input style={{ width: '33%' }} defaultValue={user.email} />
            </Form.Item>
            <Form.Item label="Website">
                <Input style={{ width: '33%' }} defaultValue={user.website} />
            </Form.Item>
            {/* Add other fields as needed */}
            <Form.Item>
                <Button type="primary" onClick={saveChanges} loading={saving}>Save Changes</Button>
            </Form.Item>
        </Form>
    );
};

export default Profile;
